/* Style the top navigation bar */
.topnav {
  overflow: hidden;
  background-color: #333;
}

/* Style the topnav links */
.topnav a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

/* Change color on hover */
.topnav a:hover {
  background-color: #ddd;
  color: black;
}

/* Style the content */
.content {
  padding-left: 5%;
  padding-right: 10%;
  padding-top: 32px;
  padding-bottom: 2%;
  text-align: justify;
  background-color: #ddd;
}

.date {
  font-size: 14px;
  font-style: italic;
}

.subject {
  margin-top: 4px;
  margin-bottom: 4px;
  font-size: 32px;
  font-weight: bold;
}


/* Style the footer */
.footer {
  background-color: #f1f1f1;
  padding: 10px;
}

.topbanner {
    text-align: center;
    font-size: 80px;
    font-family: "Roboto", "Open Sans";
    padding: 14px 16px;
    background-color: #ff9999;
}
